.page {
  margin: 20px 10px;
}
.page .body {
  min-height: 80vh;
}
.page .body .articleItem {
  cursor: pointer;
  border-bottom: 1px solid #e8e8e8;
}
.page .body .articleItem:hover {
  background-color: #e8e8e8;
}
.page .body .articleItem .metaList {
  display: flex;
  align-items: baseline;
  white-space: nowrap;
}
.page .body .articleItem .metaList .metaItem {
  color: #b2bac2;
}
.page .body .articleItem .metaList .metaItem:not(:last-child)::after {
  content: "·";
  color: #b2bac2;
  margin: 0px 0.4em;
}
.page .body .articleItem .articleTitle {
  margin: 0;
  padding: 0;
}
